import React, { Component } from "react";
import { Redirect } from "react-router-dom";

import JuneBlog2 from "../pages/june/Blog2";
import JulyBlog2 from "../pages/july/Blog2";

const posts = {
  may: {},
  june: {
    blog2: JuneBlog2
  },
  july: {
    blog2: JulyBlog2
  }
};

class Post extends Component {
  render() {
    const {
      match: {
        params: { month, post }
      }
    } = this.props;
    const postsForMonth = posts[month];
    const BlogPost = !!postsForMonth ? postsForMonth[post] : null;

    return !!BlogPost ? (
      <BlogPost />
    ) : !!postsForMonth ? (
      <Redirect to={`/${month}`} />
    ) : (
      `There are no blog posts for ${month}`
    );
  }
}

export default Post;



