import React, { Component } from "react";

class About extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>About</h2>

          <h3>Who are you?</h3>
          <p>"My name is Gary, and I am a noob at coding. Short list: "</p>
          <ol>
            <li>
              I am <em>not</em> an accomplished software developer. Not yet, at
              least.
            </li>
            <li>I was born and raised in Cincinnati.</li>
            <li>I love soccer.</li>
            <li>I am hungry.</li>
          </ol>
          <h3>Why are you here?</h3>
          <p>"...wait, what?"</p>

          <h3>Stupid says what?</h3>
          <p>"Wha-</p>
          <p>"Wait, hold on!"</p>

          <h3>This blog sucks.</h3>
          <p>"..."</p>

          <h3>How many people have viewed your "blog", Gary?</h3>
          <p>
            "Uh, nobody has really seen it yet. It's still a work in progress."
          </p>

          <h3>So you're the only person who has seen this?</h3>
          <p>"..."</p>

          <h3>You're doing great work, Gary. Great work.</h3>
          <p>"Thanks."</p>
        </div>
      </div>
    );
  }
}

export default About;



