import React, { Component } from "react";
import CodeExample from "../../components/CodeExample";

class JulyBlog2 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>The One About Objects</h2>
          <p>Date: 7/24/18</p>
          <p>Location: Longworth Hall</p>

          <h3>A (Somewhat) Fresh Start</h3>
          <p>
            July is coming to a close, which means the call volume in the
            customer service department is, to the relief of many, on the down
            slope. Our total daily call numbers confirm this trend, so we are
            all getting a chance to take a breather and really enjoy the down
            time. Hoorayyyyy!
          </p>

          <p>
            In addition to that good news, I will be working in Account
            Operations (AOOOOOOO) each Wednesday from 8am-4pm and then meeting
            with Brandon for the last hour of the day. We finally - FINALLY! -
            are going to have the meetings we were guaranteed when all of this
            began. This is a welcomed change, to say the least.
          </p>

          <p>
            With the decline in call volume and schedule changes, that down time
            has been put to great use with this week's the homework assignment.
            This past week has been all about objects: what they are, how to use
            them, and the different ways they can be accessed. So, without
            further adeu...
          </p>

          <h3>Objectively Speaking</h3>
          <CodeExample
            codeString={`
          problem1(person) {
            let fullName = person.firstName + " " + person.lastName;
            return fullName;
          }

          problem2(people, age) {
            let result = {};
        
            for (let personName in people) {
              const personAge = people[personName];
        
              if (personAge <= age) {
                result[personName] = personAge;
              }
            }
          `}
          />

          <h3>From Object to Array and Back Again</h3>
          <CodeExample
            codeString={`
            problem3(people) {

              let result = [];
              for (let person in people) {
                let personObject = {
                  age: people[person],
                  name: person
                };
          
                result.push(personObject);
              }
              return result;
            }
          `}
          />

          <h3>Looking Ahead</h3>

          <p>Woooooo!! Let's go!</p>
        </div>
      </div>
    );
  }
}

export default JulyBlog2;



